* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 18px;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.top-bar-social {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px 28vw 0 10vw;
  background: rgba(0, 0, 0, 0.9);
  font-size: 2.5rem;
  z-index: 1;
}

.burger-btn {
  position: fixed;
}

.top-bar-social a {
  margin-right: 0.5rem;
}

.top-bar-social a {
  color: red;
}

.reg-btn {
  font-size: 2rem;
  color: #fff;
  padding: 5px 20px;
  font-variant: small-caps;
  border: none;
  cursor: pointer;
  border-radius: 30px;
  background: red;
}

.top-bar-btns {
  margin-bottom: 7vw;
}

.top-bar-btns button:first-child {
  margin-right: 15px;
}

.bottom-btn {
  margin-bottom: 2rem;
}

.reg-btn:hover {
  animation: btn-beat 1s infinite;
}

@keyframes btn-beat {
  50% {
    transform: scale(1.1);
  }
}

.footer {
  height: 100px;
  background: linear-gradient(90deg, black, red);
  margin-top: -100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #eee;
  padding: 3rem;
}

.wrapper {
  background: fixed center no-repeat url('./images/test/bg.jpg');
}

.container {
  min-height: 100vh;
  padding-bottom: 60px;
  background-color: rgba(0, 0, 0, 0.7);
}

.top-bg {
  background: fixed no-repeat url('./images/test/top-bg.png');
  background-size: 100% auto;
}

.top-fg {
  min-height: calc(100vw * 0.5625);
  background: no-repeat url('./images/test/top-fg.png');
  background-size: 100% auto;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
}

.sim-screen {
  height: 900px;
  background: center no-repeat url('./images/test/mid-screen.jpg');
  clip-path: polygon(100% 0, 100% 90%, 50% 100%, 0 90%, 0 0);
}

.sim-info {
  height: 92%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-size: 2rem;
}

.sim-info-top {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.rules-and-prizes {
  margin: 2rem 0;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.rules {
  counter-reset: li;
  list-style: none;
  *list-style: decimal;
  padding: 0;

  margin: 2rem 2rem 2rem 3rem;
}

.rules span {
  position: relative;
  display: block;
  padding: 0.4em 0.4em 0.4em 2em;
  *padding: 0.4em;
  margin: 0.8em 0;
  background: rgba(255, 255, 255, 0.8);
  color: #333;
  text-decoration: none;
  border-radius: 0.3em;
  transition: all 0.3s ease-out;
}

.rules span:hover {
  background: #eee;
}

.rules span:hover::before {
  transform: rotate(360deg);
}

.rules span::before {
  content: counter(li);
  counter-increment: li;
  position: absolute;
  left: -1.3em;
  top: 50%;
  margin-top: -1.3em;
  background: #bd2929;
  height: 2em;
  width: 2em;
  color: #fff;
  line-height: 2em;
  border: 0.3em solid #fff;
  text-align: center;
  font-weight: bold;
  border-radius: 2em;
  transition: all 0.3s ease-out;
}

.prizes {
  padding: 2rem;
  margin-right: 2rem;
  min-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  border-radius: 20px;
  text-align: center;
  border: 4px solid #fff;
}

.prizes h2 {
  font-size: 1.4rem;
}

.prizes h3 {
  width: 100%;
  margin-top: 1rem;
  padding-top: 0.5rem;
  margin-bottom: 0.5rem;
  border-top: 2px solid #fff;
}

.flex-line {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.classes {
  height: 900px;
  background: center no-repeat url('./images/test/class_t.jpg');
  background-size: auto;
  clip-path: polygon(100% 20%, 100% 90%, 50% 100%, 0 90%, 0 0);

  font-size: 2rem;
  color: #fff;
  font-variant: small-caps;
}

.qual {
  position: relative;
  height: 100%;
  background: no-repeat url('./images/test/qual.png');
  background-position-x: center;
  background-position-y: 5rem;

  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
}

.qual span {
  text-align: center;
  max-width: 460px;
  margin-bottom: 4rem;
  margin-top: 300px;
}

.sim-bg {
  margin-top: -100px;
  background: fixed center url('./images/test/sim-bg.jpg');
}

.sim {
  margin: 0 1rem;
  padding-top: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  font-variant: small-caps;
  font-size: 1.5rem;
}

.sim p {
  max-width: 800px;
  text-align: center;
  padding-bottom: 1rem;
}

.sim-info-bot {
  position: relative;
}

.arrow {
  position: absolute;
  left: 50%;
  margin-left: -15px;
  margin-top: 20px;
  width: 30px;
  height: 30px;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
}

.arrow-bottom {
  position: absolute;
  left: 50%;
  margin-left: -15px;
  bottom: 10px;
  width: 30px;
  height: 30px;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
}

.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

.qual-top-bg {
  background: #000;
  
}

.qual-top {
  min-height: min(45vw, 500px);
  background: center/contain no-repeat url('./images/test/top.jpg');

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.shadow {
  position: relative;
  border-bottom: 2px solid #bd2929;
  box-shadow: 5px -10px 15px 5px rgba(255, 0, 0, 1);
}

.top-results {
  max-width: 1200px;
  width: 100%;
  color: #fff;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  padding-right: 100px;
}

.results-header {
  margin-top: 50px;
}

.results-name {
  text-align: right;
  font-size: 2.5rem;
  margin-right: 20px;
}

.results-time {
  margin-bottom: 30px;
  font-size: 2rem;
}

.list-of-profiles {
  max-width: 1200px;
  margin: 10px auto;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  border-radius: 10px;
  border: 1px solid #bd2929;
}

.list-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.5rem;
  border-radius: 10px;
  margin-bottom: 0.5rem;
  cursor: pointer;
}

.list-item.qualified {
  background: #bd29297a;
}

.list-item.none-qualified {
  background: #4d4d4d7a;
}

.list-item:last-child {
  margin-bottom: 0;
}

.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  opacity: 0;
  pointer-events: none;
  transition: .5s;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal-content {
  position: relative;
  padding: 20px;
  border-radius: 12px;
  background-color: #000;
  color: #fff;
  width: 400px;
  height: 200px;
  transform: scale(0.5);
  transition: 0.4s all;
}

.modal-content.active {
  transform: scale(1);
}

.close-btn {
  position: absolute;
  top: .5rem;
  right: .5rem;
  font-size: 24px;
  color: red;
  background: #000;
  border: none;
  cursor: pointer;
}

.modal-view {
  display: flex;
  flex-direction: row;
}

.modal-pic {
  width: 60%;
  height: 120px;
  background: url('./images/car01.jpg');
}

.modal-text {
  display: flex;
  flex-direction: column;
}

@media (max-width: 1200px) {
  .top-bar-btns button {
    font-size: 1.5rem;
  }

  .rules-and-prizes {
    flex-direction: column;
  }

  .rules {
    margin: 0 1rem 0 2rem;
    font-size: 16px;
  }

  .prizes {
    margin: 1rem 0 0;
  }
}

@media (max-width: 800px) {
  .top-bg {
    background-position-y: 1rem;
    margin-top: 1rem;
  }

  .top-bar-btns {
    margin-bottom: 4vw;
  }

  .sim-screen {
    height: 600px;
    background-size: 200%;
  }

  .classes {
    height: 600px;
    font-size: 1.5rem;
  }

  .sim-info {
    font-size: 1.5rem;
  }

  .sim-info-top {
    margin-top: 1rem;
  }

  .qual {
    background-size: 100%;
  }

  .arrow {
    margin-top: 10px;
  }
}

@media (max-width: 600px) {
  .top-bg {
    background-position-y: 2rem;
    margin-top: 2rem;
  }

  .top-bar-btns {
    margin-bottom: 0.5rem;
  }

  .top-bar-btns button {
    font-size: 1rem;
  }

  .rules {
    margin: 0 0.5rem 0 0.5rem;
  }

  .prizes {
    min-width: 350px;
    margin-left: -1rem;
  }

  .sim-screen {
    height: 400px;
    background-size: 200%;
  }

  .rules-and-prizes {
    flex-direction: column;
    margin: 2rem 0.5rem 2rem 1.5rem;
  }

  .sim-info {
    height: 90%;
    font-size: 1rem;
  }

  .classes {
    height: 400px;
    background-size: 200%;
    font-size: 1rem;
  }

  .sim {
    padding-top: 120px;
    font-size: 18px;
  }

  .arrow {
    margin-top: 12px;
    margin-left: -10px;
    width: 20px;
    height: 20px;
  }

  .arrow-bottom {
    margin-left: -10px;
    width: 20px;
    height: 20px;
  }

  .wrapper {
    background-size: cover;
  }

  .bm-burger-btn {
    width: 20px;
  }
}

@-moz-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -moz-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -moz-transform: translateY(-16px);
    transform: translateY(-16px);
  }
  60% {
    -moz-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
@-webkit-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-16px);
    transform: translateY(-16px);
  }
  60% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -moz-transform: translateY(-16px);
    -ms-transform: translateY(-16px);
    -webkit-transform: translateY(-16px);
    transform: translateY(-16px);
  }
  60% {
    -moz-transform: translateY(-8px);
    -ms-transform: translateY(-8px);
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
